      "Well I must say, Zagreb is worth visiting. I had a beautiful time there, traveled through the streets of Zagreb and met new people ,explored cool places. My memories of Zagreb range from my new kicks, two beautiful picturesque apartments that I lived in, the majestic opera house, the line of bars at Tkalieva street and having one too many beers at my favourite bar there. The Museum of broken relationships made me cry, and the funicular that took me from the lower town to the upper town made me squeal with delight. The city stayed with me and it was a beautiful place to spend some time in Autumn.Upper town or Gornji Grad,Zagreb in Zagreb comprises of beautiful pastel coloured houses and narrow windy alleyways that lead you to beautiful squares and spectacular viewing points of the town below. Gornji Grad houses all the architectural delights like St. Marks church with its spectacular rooftop colours, the expansive St. Marks square, the Croatian Parliament Building, the quaint Stone Gate, the Zagreb City Museum, the Cathedral of Zagreb well known for its twin towers and the Dolac Market.",
