      "I returned last night from a week-long vacation in Mexico City. My trip unintentionally, but fortuitously, coincided with Da de los Muertos. I stayed at Sofitel Mexico City Reforma. The hotel is conveniently located in the center of the city. It has spectacular views and excellent customer service. The hotel's staff enforced the necessary safety measures to keep its guest safe and healthy. For what it's worth, it is located next to the American Embassy. I lived in New York City for five years and, possibly as a result, I never felt unsafe in Mexico City. I took Ubers everywhere and all of the drivers wore masks and often had hand sanitizer near the back seat.I visited numerous art museums, toured a few historical homes, and ate at numerous cafes and restaurants. In regards to art museums, I highly recommend Anahuacalli Museum, Museo Frida Kahlo, Museo Nacional de Arte, Museo Soumaya, and Pallacio de Bellas Artes. Museo Frida Kahlo is a must-see and admission entitles you to admission at Anahuacalli Museum, Diego Rivera's museum of pre-Hispanic artifacts. I cannot stress enough the importance of purchasing your tickets in advance online for the Museo Frida Kahlo. The line was very short, but there were people without tickets and they were told to purchase their tickets online for the following day.In regards to historical homes, I highly recommend Casa de la Bola and Casa Gilardi. Casa de la Bola was probably built in the 1800s and was last occupied by Antonio Haghenbeck from the early 1940s until his death in the early 1990s. The house is grand, furnished lavishly, and has a beautiful tropical garden. Casa Gilardi is the last home designed by architect, Luis Barragn. It was completed in 1976. It is airy and bright.Finally, I highly recommend Salve Casa Niza and Cafe Nin for coffee and pastries. I highly recommend Arango, Azul, Carmela y Sal, Huset and Limosneros. I had some incredible street food too.No trip to Mexico City would be complete without a visit to Churrera El Moro for churros, Pastelera Ideal for pastries, and a day trip to Teotihuacn.Please feel free to ask me questions if you're considering a trip to Mexico City or if you have one planned.",
