    storyText:
      "If you are planning a visit to the Taj Mahal in India and you are an information freak like me, you will read every Taj Mahal travel blog you can get your hands on.Because you like to be prepared.Or at least your husband does.But unfortunately, every Taj Mahal blog or guide that you find seems to be filled with heaps of negative stuff. Giving you warnings about how your trip to this world wonder may be a traumatic experience and leave you mentally scarred forever.You read about robbers and touts and selfie seekers and all of a sudden you become wary about visiting the Taj Mahal Agra  its not exactly the once in a lifetime experience you anticipated, is it?We have just travelled around India for six weeks and let me assure you, you mustnt believe all the everything you read in travel blogs. A lot of the travel tips I read about were utter rubbish, and the Taj Mahal Agra was no exception. There are things you need to know about the Taj so that you can relax and enjoy this beautiful once in a lifetime experience as it is meant to be.If you REALLY want to know what it is like to take two kids backpacking around India then check out our travel podcast  Backpacking India. Is it really that bad?Before you leave home dont forget to use our awesome travel checklist   there are items on that list that came in very handy in India!",
